.intro {
    width: 100%;
    height: 100vh;
    background-position: center;
    background-size: cover;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
}

.bg{
    position: absolute;
    top: 9rem;
    right: 25px;
    z-index: -5;
    object-fit: cover;
    height: 75vh;
}
.introContent{
    height: 120vh;
    width: 100vw;
    padding: 2rem;
    font-size: 4rem;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1.75rem;
    font-weight: 200;
}
.introName{
    color: yellow;
    font-weight: 400;
}
.introPara{
    font-size: medium;
    font-weight: 200;
    letter-spacing: 1px;
}
.btn{
    background-color: white;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}


@media screen and (max-width:780px) {

.introContent {
    height: 152vh;
    width: 100vw;
    padding: 2rem;
    font-size: 2rem;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
    
}


@media screen and (max-width:500px) {
    .bg {
        position: absolute;
        top: 9rem;
        right: -7px;
        z-index: -5;
        object-fit: cover;
        height: 75vh;
    }
    
}
@media screen and (min-width:1640px) {
    .introContent {
        height: 120vh;
        width: 100vw;
        padding: 2rem;
        font-size: 5rem;
        font-weight: 600;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    
}