#contactMe{
    min-height: calc(68vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contactTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}

.contactDesc{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}
.contactform{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background-color: rgb(40, 40, 40);
}

.submitBtn{
    background-color: white;
    border: none;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
    border-radius: 0.5rem;
}
.info{
    max-width: 100rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    
}
.conatactInfoImg{
    height: 2rem;
}
.contactInfo h2{
    font-size: small;
    color: aliceblue;
}
.contactInfo{
    margin: 2px;
    padding: 3px;
    height: 5rem;
    width: 9rem;
}


@media screen and (max-width:930px) {
    
.conatactInfoImg{
    height: 1rem;
}
.contactInfo h2{
    font-size: 10px;
    color: aliceblue;
}
.contactInfo{
    margin: 10px;
}
}


@media screen and (max-width:330px) {
    
    .conatactInfoImg{
        height: 1rem;
    }
    .contactInfo h2{
        font-size: 10px;
        color: aliceblue;
    }
    .contactInfo{
        margin: 1px;
    }
    }