#works {
  overflow: hidden;
  width: 100vw;
  max-width: 90rem;
  min-height: calc(100vh - 4rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 35px auto;
  padding: 0 2rem;
}

.workstitle {
  margin: 1rem 0;
  font-size: 3rem;
}
.workdesc {
  font-weight: 300;
  font-size: 1rem;
  max-width: 45rem;
  margin-bottom: 20px;
}

.card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.project {
  background-color: rgb(40, 40, 40);
  width: 40rem;
  height: 30rem;
  margin: 10px;
  padding: 20px;
  border-radius: 1rem;
}
.project:hover{
    transform: scale(1.01);
}

.projectimg {
  margin: 1rem 0;
  object-fit: cover;
}

@media screen and (max-width: 600px) {
  .workdesc {
    font-size: 0.8rem;
  }
  .workstitle {
    font-size: 2rem;
  }
}
@media screen and (max-width: 1400px) {
  .project {
    background-color: rgb(40, 40, 40);
    width: 28rem;
    height: 25rem;
    margin: 10px;
    padding: 20px;
    border-radius: 1rem;
  }
  .projectimg {
    margin: 1rem 0;
    object-fit: fill;
    height: 15rem;
  }
}

@media screen and (max-width: 1000px) {
  .project {
    background-color: rgb(40, 40, 40);
    width: 20rem;
    height: 23rem;
    margin: 10px;
    padding: 20px;
    border-radius: 1rem;
  }
  .projectimg {
    width: 18rem;
    margin: 1rem 0;
    object-fit: cover;
    height: 10rem;
  }
}

@media screen and (max-width: 760px) {
