#skills{
    overflow: hidden;
    width: 100vm;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.skillDesc{
    font-weight: 200;
    font-size: 1rem;
    max-width: 65rem;
    padding: 0 2rem;
    font-weight: 300;
}

.skillsBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 88%;
    text-align: left;
}
.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background-color: rgb(50, 50, 50);
}
.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}
.skillBarText p{
    font-size: 1rem;
    font-weight: 300;
}

@media screen and (max-width:840px) {
    #skills {
        overflow: hidden;
        width: 200vm;
        max-width: 100rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin: 0px;
    }
    
}
@media screen and (max-width:500px) {
    .skillBarText p {
        font-size: 0.7rem;
        font-weight: 300;
    }
    .skillBarText h2{
            display: block;
            font-size: 1rem;
            font-weight: bold;
    }
    .skillDesc{
        font-weight: 200;
        font-size: 0.8rem;
        max-width: 65rem;
        padding: 0 2rem;
        font-weight: 300;
    }
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;
    }
}
