.navbar{
    background-color: rgb(30, 30, 30);
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 5%;
    font-size: 1.5rem;
}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}
.desktopMenuListItem{
    margin: 2rem;
    cursor: pointer;
}
.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.05rem;
    border-bottom: 3px solid #ffff00;
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}
.desktopMenuBtn{
    border: none;
    background-color: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    font-weight: 600;
    font-size: 1.5rem;
}
.desktopMenuBtn:hover{
    background-color: #ffff00;
}

.active{
    color: #ffff00;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.navMenu{
    position: absolute;
    top: 6rem;
    right: 2rem;
    z-index: 100;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background-color: rgb(40, 40, 40);
    border-radius: 1rem;
}
.ListItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background-color: rgb(30, 30, 30);
    border-radius: 1rem;
}
.mobmenu{
    display: none;
    object-fit: cover;
    height: 3rem;
}


@media screen and (max-width:1000px) {
    .navbar{
        font-size: 1rem;
    }
    .desktopMenuListItem {
        margin: 1rem;
        cursor: pointer;
    }
}

@media screen and (max-width:580px) {
    .mobmenu{
        display: flex;
    }
    
    .desktopMenuListItem{
        display: none;
    }
}